html {
  height: 100%; }

body {
  min-height: 100%;
  font-family: "Encode Sans Expanded", sans-serif;
  background: #fff;
  font-size: 14px;
  margin: 0; }
  @media (min-width: 1600px) {
    body {
      font-size: 16px; } }

section,
header,
footer {
  position: relative;
  z-index: 2; }

h1,
h2,
h3,
h4,
p,
ul {
  margin: 0; }

h1 {
  font-size: 3.666em; }

h2 {
  font-size: 2.618em; }

h3 {
  font-size: 1.931em; }

