    margin: 10px; }
  body {
    font-size: 16px; }
  section {
    position: relative;
    top: -100px; }
    section:nth-child(4) {
      margin-top: 100px; }
  .section__text {
    max-width: 600px; }
  .contact {
    display: flex;
    flex-direction: row;
    margin: 2em 0;
    position: relative;
    top: -8.2em;
    justify-content: space-between; }
    .contact a {
      padding-top: 1em; }
  .footer {
    position: absolute;
    bottom: 1em;
    padding-right: 1em; } }

.App {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  padding-top: 3em; }
  .App__header {
    margin-bottom: 2em; }

.A4 {
  max-width: calc(592px * 1.5);
  padding: 20px; }

.container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 2em;
  grid-row-gap: 0.5em;
  grid-template-areas: "header header header header" "exp exp skills skills" "edu edu interests interests" "footer footer footer footer";
  background: #fff;
  color: #020203;
  overflow: hidden;
  position: relative; }
  @media (max-width: 660px) {
    .container {
      display: flex;
      flex-direction: column; } }

.header {
  grid-area: header; }

.contact {
  grid-area: contact; }

.experience {
  grid-area: exp; }

.education {
  grid-area: edu; }

.icons {
  grid-area: icons;
  display: grid;
  grid-template-rows: 1fr 1fr; }

.interests {
  grid-area: interests; }

.skills {
  grid-area: skills; }

.footer {
  grid-area: footer;
  margin-top: 1.5em; }

.header {
  color: #575b66;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "inner inner contact";
  margin-bottom: 2em; }
  @media (max-width: 660px) {
    .header {
      grid-template-columns: 1fr;
      grid-template-areas: "inner" "contact"; } }
  .header__inner {
    grid-area: inner; }
  .header__title {
    color: #020203;
    position: relative; }
  .header__text {
    padding-top: 1.5em; }

.footer__clause {
  padding-bottom: 1em;
  text-align: justify; }

.footer__author {
  text-align: center; }

.lines {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }
  .lines__item {
    width: 12.5%;
    float: left;
    height: 100%; }
    .lines__item:before {
      content: '';
      height: 100%;
      width: 1px;
      background: #f0f2f7;
      display: block; }

.contact {
  display: flex;
  color: #575b66;
  flex-direction: column;
  grid-area: contact; }
  .contact__icons {
    display: flex;
    margin-top: 1em;
    justify-content: flex-end;
    flex: 1 1; }
  .contact .icon {
    width: 2em;
    height: 2em; }
    .contact .icon img {
      width: 100%; }
    .contact .icon:not(:first-child) {
      margin-left: 1em; }

.section {
  position: relative;
  z-index: 2;
  margin-bottom: 2em; }
  .section__title {
    padding-bottom: 0.33em; }
  .section__body {
    padding-top: 0.66em; }
  .section__item {
    padding-left: 1em;
    margin-bottom: 1em;
    z-index: 1;
    transition: all 0.3s ease-out 1s;
    position: relative; }
    .section__item__heading {
      margin: 0;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center; }
      .section__item__heading h5 {
        margin: 0.4em 0; }
      .section__item__heading:before {
        content: "";
        display: block;
        position: absolute;
        left: 0.85em;
        top: 1px;
        height: 1px;
        width: 10%;
        z-index: -1;
        opacity: 0.5;
        background: #d8af00;
        transition: 0.2s ease-in-out 0.22s; }
  .section__row {
    position: relative; }
    .section__row::before {
      content: "â€¢ ";
      color: #d8af00;
      position: absolute;
      left: -1em; }
  .section__heading {
    position: relative;
    margin: 5px 0; }
    .section__heading:before {
      content: "";
      color: #d8af00;
      position: absolute;
      left: -1.7em;
      top: 0;
      font-size: 1em; }

