@media print {
  @page {
    margin: 10px;
  }
  .App {
  }
  body {
    font-size: 16px;
  }

  #container {
  }
  section {
    position: relative;
    top: -100px;
    &:nth-child(4) {
      margin-top: 100px;
    }
    }

  .section__text {
    max-width: 600px;
  }

  .contact {
    display: flex;
    flex-direction: row;
    margin: 2em 0;
    position: relative;
    top: -8.2em;
    justify-content: space-between;
    a {
      padding-top: 1em;
    }
  }

  .footer {
    position: absolute;
    bottom: 1em;
    padding-right: 1em;
  }
}
